/// <reference no-default-lib="true"/>

<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import echeckService from '@/service/echeck.service';

@Options({
  props: {
    'xpkg': Object
  },
  methods: {
    download: function(version: any) {
      echeckService.getXPackage(version).then(response => window.open(response.data.downloadUrl))
    }
  }
})
export default class XPackage extends Vue {
  setup() {
    return { echeckService }
  }
}
</script>

<template>
<div class="componennt">
  <div class="pcard">
    <p>
      {{xpkg.fileName}}
    </p>
    <p>
      {{xpkg.version}}
    </p>
    <p>
      {{$filters.formatDate(xpkg.releaseDate)}}
    </p>
    <p>
      {{$filters.formatSize(xpkg.sizeBytes)}}
    </p>
    <button v-on:click="download(xpkg.version)" class="btn" :class="{'btn-primary': !xpkg.latest, 'btn-danger': xpkg.latest }">Download v{{xpkg.version}}</button>
  </div>
</div>
</template>

<style scoped lang="scss">
.componennt {
 background-color: gray;
}
.pcard {
  padding: 3rem;
  background-color: whitesmoke;
  border-radius: 0.3rem;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  transition: 0.3s;
}

.pcard:hover {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.btn:hover{
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
</style>