<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import XPackages from '@/components/XPackages.vue'; // @ is an alias to /src
import echeckService from '@/service/echeck.service';

@Options({
  components: {
    XPackages,
  },
  data () {
    return {
      xpkgsData: null
    }
  },
  mounted () {
    echeckService.getXPackages().then(response => (this.xpkgsData = response.data))
  },
})
export default class HomeView extends Vue {}
</script>

<template>
  <div class="home">
    <img src="images/rhino.png">
    <div>ECheck Rhino Plugin Packages</div>
    <x-packages :xpkgs="xpkgsData" />
  </div>
</template>

<style scoped lang="scss">
img {
  width: 3rem; height: 3rem;
}

.home {
  max-width: 70%;  
  min-width: fit-content;
  margin-left: auto;
  margin-right: auto;
}
</style>